import React from 'react'

import './title_public.css'
import './story.css'

import storyBackground from './images/storySection01Bg.png'
import storySec03BG from './images/storySection3.png'
// import storyBackground02 from './images/'

function story(){
    let storyBg={
        padding:"160px 323px",
        background: `url(${ storyBackground }) 0 0 no-repeat`,
    }
    let storySec3={
        marginBottom:"-4px",
        background:`url(${storySec03BG}) 0 0 no-repeat`
    }
    // let storyBg02={
    //     background:url()
    // }
    return (
        <div className="story container">
            <section style={storyBg} className="title">
                <span className="en-title en-title-story">FAIRY TALES SERIES</span>
                <div className="cn-title cn-title-story">
                    <hr/>
                    <div></div>
                    <h1></h1>
                    <div></div>
                    <hr/>
                </div>
                <p className="story-describe"><br/></p>
            </section>
            <section className="ancientStory">
                <div className="section01Left">
                    <img src={require("./images/storySection2Left.png")} alt=""/>
                    <h2></h2>
                    <hr/>
                    <span></span>
                </div>
                <div className="section01Right">
                    <h2></h2>
                    <hr/>
                    <p>
                        <br/>
                        <br/>
                        <br/>
                        
                    </p>
                    <a href="#">MORE</a>
                </div>
            </section>
            <section className="container">
                <div className="story-product"  style={storySec3}>
                    <div className="section03Left">
                        <h3>CHILDREN'S STORIES</h3>
                        <div>
                            <img src={require("./images/storySection3Left01.png")} alt=""/>
                            <h3></h3>
                            <hr/>
                        </div>
                        <div>
                            <img src={require("./images/storySection3Left02.png")} alt=""/>
                            <h3></h3>
                            <hr/>
                        </div>
                    </div>
                    <div className="section03Right">
                        <img src={require("./images/storySection3Right.png")} alt=""/>
                        <h3></h3>
                        <hr/>
                        <p></p>
                    </div>
                </div>
            </section>
        </div>
    )
}
export default story


