import React,{Component} from 'react'

import copyState from './copyState'
import './goodsCategory.css'
import section02Background from './images/categorySection02.png'

class goodsCategory extends Component {
    constructor(props){
        super(props)
        this.state={
            onsaleSection:0,
            sofaDisplay:0
        }
    }
    changeSofaDisplay(bol){
        let state=copyState(this.state)
        bol?state.sofaDisplay+=1:state.sofaDisplay-=1
        this.setState(state)

    }
    addShopList(){
        
    }

    render(){
        let headerShopGoods={
            title:'STOCKHOLM ',
            goodsType:'',
            color:'',
            price:'1,299.00'
        }
        let section02Bg={
            background: `url(${ section02Background }) 0 0 no-repeat`,
        }
        return (
            <div className="shop-category">
               <div className="shop-category-section01">
                    <div className="shop-category-section01-bg"></div>
                    <div className="container">
                        <div className="shop-category-section01Img"><img src={require('./images/categorySection01.png')} alt=""/></div>
                        <div className="shop-category-section01-describe">
                            <h1>STOCKHOLM<br/></h1>
                            <p> </p>
                            <div>
                                <div></div>
                                <div></div>
                            </div>
                            <span>  1,299.00</span>
                            <a href="javascript:void(0)" onClick={this.props.onAddShopList.bind(null,headerShopGoods)}></a>
                            <a href="javascript:void(0)"></a>
                        </div>
                    </div>
                </div>
                <div className="container shop-category-section02">
                    <div className="shop-category-section02-bg" style={section02Bg}>
                        <div>
                            <hr/>
                            <h1></h1>
                        </div>
                        <div className="shop-category-section02-img-position">
                            <img src={require(`${Math.abs(this.state.sofaDisplay%3===0)?'./images/categorySection02left.png':this.state.sofaDisplay%3===1?'./images/categorySection02middle.png':'./images/categorySection02right.png'}`)} alt=""/>
                            <div className="shop-category-section02-describe">
                                <h4>{Math.abs(this.state.sofaDisplay%3)===0?'':Math.abs(this.state.sofaDisplay%3)===1?' ':' '}</h4>
                                <span>{Math.abs(this.state.sofaDisplay%3)===0?'692.217.49':Math.abs(this.state.sofaDisplay%3)===1?'990.318.18':'490.904.76'}</span>
                                <p>{Math.abs(this.state.sofaDisplay%3)===0?'1,420,00':Math.abs(this.state.sofaDisplay%3)===1?'1,499,00':'799,00'}</p>
                                <a href="javascript:void(0)">
                                    <div>
                                        <svg class="icon" width="11px" height="11.00px" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg">
                                            <path fill="#fff" d="M1023.92832 477.88032 546.25792 477.88032 546.25792 0.20992 478.01856 0.20992 478.01856 477.88032 0.34816 477.88032 0.34816 546.11968 478.01856 546.11968 478.01856 1023.79008 546.25792 1023.79008 546.25792 546.11968 1023.92832 546.11968Z" />
                                        </svg>
                                    </div>
                                    <span></span>
                                </a>
                            </div>
                        </div>
                    </div>
                    <div className="shop-category-section02-switch">
                        <div onClick={this.changeSofaDisplay.bind(this,true)}>
                            <svg class="icon" width="60px" height="44.00px" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg">
                                <path fill="#666666" d="M128 480h645.6L530.4 237.6 576 192l274.4 274.4L896 512l-45.6 45.6L576 832l-45.6-45.6L773.6 544H128v-64z" />
                            </svg>
                        </div>
                        <div onClick={this.changeSofaDisplay.bind(this,false)}>
                            <svg class="icon" width="60px" height="44.00px" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg">
                                <path fill="#666666" d="M896 544H250.4l242.4 242.4L448 832 173.6 557.6 128 512l45.6-45.6L448 192l45.6 45.6L250.4 480H896v64z" />
                            </svg>
                        </div>
                    </div>
                </div>                
                <div className="shop-category-section03">
                    <div><img src={require('./images/categorySection03.png')} alt=""/></div>
                    <div>
                        <hr/>
                        <h3>VALLENTUNA </h3>
                        <span>391.612.90</span>
                        <p></p>
                        <a href="javascript:void(0)"></a>
                    </div>
                </div>
                <div className="shop-category-section04">
                    <div className="shop-category-section04-discribe">
                        <div>
                            <h3>{Math.abs(this.state.onsaleSection%3)===0?' ':Math.abs(this.state.onsaleSection%3)===1?' ':' '}</h3>
                            <span>{Math.abs(this.state.onsaleSection%3)===0?'591.359.45':Math.abs(this.state.onsaleSection%3)===1?'099.008.31':'002.623.70'}</span>
                            <p>{Math.abs(this.state.onsaleSection%3)===0?''
                            :Math.abs(this.state.onsaleSection%3)===1?''
                            :''}</p>
                        </div>
                        <div className="shop-category-section04-switch">
                            <div>
                                <svg class="icon" width="60px" height="44.00px" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg">
                                    <path fill="#666666" d="M896 544H250.4l242.4 242.4L448 832 173.6 557.6 128 512l45.6-45.6L448 192l45.6 45.6L250.4 480H896v64z" />
                                </svg>
                            </div>
                            <div>
                                <svg class="icon" width="60px" height="44.00px" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg">
                                    <path fill="#666666" d="M128 480h645.6L530.4 237.6 576 192l274.4 274.4L896 512l-45.6 45.6L576 832l-45.6-45.6L773.6 544H128v-64z" />
                                </svg>
                            </div>                        
                        </div>                        
                    </div>
                    <div className="shop-category-section04-goodImage">
                        <div>Sale<br/>50%</div>
                        <div><img src={require(`${this.state.onsaleSection%3===0?'./images/categorySection04L.png':this.state.onsaleSection%3===1?'./images/categorySection04.png':'./images/categorySection04R.png'}`)} alt=""/></div>
                    </div>
                </div>
                <div className="shop-category-section05">
                    <h4></h4>
                    <hr/>
                    <div>
                        <span></span>
                        <ul>
                            <li>
                                <img src={require('./images/categorySection05L.png')} alt=""/>
                                <div>
                                    <span></span>
                                    <a href="#"></a>
                                </div>
                            </li>
                            <li>
                                <img src={require('./images/categorySection05M.png')} alt=""/>
                                <div>
                                    <span></span>
                                    <a href="#"></a>
                                </div>
                            </li>
                            <li>
                                <img src={require('./images/categorySection05R.png')} alt=""/>
                                <div>
                                    <span></span>
                                    <a href="#"></a>
                                </div>
                            </li>
                        </ul>
                    </div>
                </div>
            </div>
        )
    }
}

export default goodsCategory


