import React,{Component} from 'react'

import ReplyDialog from './replyDialog'
import './title_public.css'
import './contact.css'

class contact extends Component{
    constructor(props){
        super()
        this.state={
            replyDialog:false
        }
    }
    toggleDialog(bol){
        this.setState({replyDialog:bol})
    }
    render(){
        return (
            <section className="contactus">
                <div className="title">
                    <span className="en-title">CONTACT US</span>
                    <div className="cn-title">
                        <hr/>
                        <div></div>
                        <h1></h1>
                        <div></div>
                        <hr/>
                    </div>
                    <p>
                        <br/>
                        <br/>
                        <br/>
                        
                    </p>
                </div>
                <div className="map">
                    <img src={require("./images/contactLocation.jpg")} alt=""/>
                </div>
                <div className="feedback">
                    <div className="title">
                        <span className="en-title">USER FEEDBACK</span>
                        <div className="cn-title">
                            <hr/>
                            <div></div>
                            <h1></h1>
                            <div></div>
                            <hr/>
                        </div>
                    </div>
                    <form className="advice-form">
                        <textarea name="" id="" cols="30" rows="10" placeholder=""></textarea>
                        <input type="text"placeholder="QQ"/>
                        <input type="text" placeholder=""/>
                        <input type="email" placeholder=""/>
                        <input type="text" placeholder=""/>
                        <button onClick={this.toggleDialog.bind(this,true)}></button>
                        {this.state.replyDialog?<ReplyDialog dialog={this.state.replyDialog} onReplyDialog={this.toggleDialog.bind(this)}/>:null}
                    </form>
                </div>
            </section>
        )
    }
}

export default contact


