import React,{Component} from 'react'

import './purchaseDetail.css'
import copyState from './copyState'

class purchaseDetail extends Component{
    constructor(props){
        super()
        this.state={
            exhibitImage:0,
            goodStyle:0,
            describeColumn:0
        }
    }
    imageChange(Num,key){
        let copystate=copyState(this.state)
        copystate[key]=Num
        this.setState(copystate)
    }
    render(){
        return(
            <div className="container">
                <div className="goodDetail-purchase">
                    <div>
                        <img src={require('./images/goodDetailMain.png')} alt=""/>
                        <ul className="goodDetail-image">
                            <li className={this.state.exhibitImage===0?'.goodDetail-image-selected':null}><a href="javascript:void(0)"><img src={require('./images/goodDetailAssistLeft.png')}  onMouseEnter={this.imageChange.bind(this,0,'exhibitImage')}alt=""/></a></li>
                            <li className={this.state.exhibitImage===1?'.goodDetail-image-selected':null}><a href="javascript:void(0)"><img src={require('./images/goodDetailAssistMiddle.png')} onMouseEnter={this.imageChange.bind(this,1,'exhibitImage')} alt=""/></a></li>
                            <li className={this.state.exhibitImage===2?'.goodDetail-image-selected':null}><a href="javascript:void(0)"><img src={require('./images/goodDetailAssistRight.png')} onMouseEnter={this.imageChange.bind(this,2,'exhibitImage')} alt=""/></a></li>
                        </ul>
                    </div>
                    <div className="goodDetail-style">
                        <h2><span></span></h2>
                        <span>691.359.40</span>
                        <p>SODERHAMN  </p>
                        <ul>
                            <li className={this.state.goodStyle===0?'good-style-selected':null} onClick={this.imageChange.bind(this,0,'goodStyle')}><a href="javascript:void(0)"><div></div></a></li>
                            <li className={this.state.goodStyle===1?'good-style-selected':null} onClick={this.imageChange.bind(this,1,'goodStyle')}><a href="javascript:void(0)"><div></div></a></li>
                            <li className={this.state.goodStyle===2?'good-style-selected':null} onClick={this.imageChange.bind(this,2,'goodStyle')}><a href="javascript:void(0)"><div></div></a></li>
                            <li className={this.state.goodStyle===3?'good-style-selected':null} onClick={this.imageChange.bind(this,3,'goodStyle')}><a href="javascript:void(0)"><div></div></a></li>
                        </ul>
                        <div className="goodDetail-purchase-setting">
                            <span></span>
                            <div className="good-detail-amount">
                                <div>
                                    <svg class="icon" width="20px" height="20.00px" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg">
                                        <path fill="#cc8c31" d="M92 484c-15.5 0-28 12.5-28 28s12.5 28 28 28l840 0c15.5 0 28-12.5 28-28s-12.5-28-28-28L92 484z" />
                                    </svg>
                                </div>
                                <input type="text" value={1}/>
                                <div>
                                    <svg class="icon" width="20px" height="20.00px" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg">
                                        <path fill="#cc8c31" d="M1023.92832 477.88032 546.25792 477.88032 546.25792 0.20992 478.01856 0.20992 478.01856 477.88032 0.34816 477.88032 0.34816 546.11968 478.01856 546.11968 478.01856 1023.79008 546.25792 1023.79008 546.25792 546.11968 1023.92832 546.11968Z" />
                                    </svg>
                                </div>
                            </div>
                        </div>
                        <h2>2,799.00</h2>
                        <div className="goodDetail-purchase-button">
                            <a href="#"></a>
                            <a href="#"></a>
                        </div>
                    </div>                
                </div>
                <div className="goodDetail-describe">
                    <ul>
                        <li className={this.state.describeColumn===0?"goodDetail-describe-column-selected":null} onClick={this.imageChange.bind(this,0,'describeColumn')}><a href="javscript:void(0)"></a></li>
                        <li className={this.state.describeColumn===1?"goodDetail-describe-column-selected":null} onClick={this.imageChange.bind(this,1,'describeColumn')}><a href="javscript:void(0)"></a></li>
                        <li className={this.state.describeColumn===2?"goodDetail-describe-column-selected":null} onClick={this.imageChange.bind(this,2,'describeColumn')}><a href="javscript:void(0)"></a></li>
                        <li className={this.state.describeColumn===3?"goodDetail-describe-column-selected":null} onClick={this.imageChange.bind(this,3,'describeColumn')}><a href="javscript:void(0)"></a></li>
                    </ul>
                    <ul>
                        <li><span>SODERHAMN </span></li>
                        <li><span></span></li>
                        <li><span></span></li>
                        <li><span></span></li>
                        <li><span>10</span></li>
                    </ul>                
                </div>
                <div className="goodDetail-other-goods">
                    <h1></h1>
                    <hr/>
                    <ul>
                        <li>
                            <a href="#">
                                <img src={require('./images/goodDetailOthersLeft.png')} alt=""/>
                                <div className="goodDetail-other-goods-name">
                                    <span></span>
                                    <span></span>
                                </div>
                                <button></button>
                            </a>
                        </li>
                        <li>
                            <a href="#">
                                <img src={require('./images/goodDetailOthersMIddle.png')} alt=""/>
                                <div className="goodDetail-other-goods-name">
                                    <span></span>
                                    <span></span>
                                </div>
                                <button></button>
                            </a>
                        </li>
                        <li>
                            <a href="#">
                                <img src={require('./images/goodDetailOthersRight.png')} alt=""/>
                                <div className="goodDetail-other-goods-name">
                                    <span></span>
                                    <span></span>
                                </div>
                                <button></button>
                            </a>
                        </li>
                    </ul>
                </div>
            </div>
        )
    }
}
export default purchaseDetail


